export default class Popper {
    // Object methods
    constructor(ref, element, config = {}) {
        Popper.init();
        this.ref = ref;
        this.element = element;
        this.element.style.position = "absolute";
        this.position = config.position ? config.position : "bottom";
        this.config = config;
        if (this.config.width) {
            this.width = this.config.width;
        }
        this.update();
        this.hide();
        Popper.instances.push(this);
        Popper.containerElement.appendChild(this.element);
    }
    // Static methods
    static init() {
        if (!this.isInitialized) {
            this.initContainer();
            window.addEventListener("resize", () => {
                for (const instance of this.instances) {
                    instance.update();
                }
            });
            this.initEvents();
            this.isInitialized = true;
        }
    }
    static initContainer() {
        if (!this.containerElement) {
            this.containerElement = document.querySelector("body > .popper");
            if (!this.containerElement) {
                this.containerElement = document.createElement("div");
                this.containerElement.classList.add("popper");
                document.body.appendChild(this.containerElement);
            }
        }
    }
    static initEvents() {
        if (!this.isInitialized) {
            document.addEventListener("click", (event) => {
                this.instances.forEach(instance => {
                    if (!(instance.element.contains(event.target) || instance.ref.contains(event.target))) {
                        if (!instance.hidden)
                            instance.hide();
                    }
                });
            });
        }
    }
    get top() {
        return parseFloat(this.element.style.top);
    }
    set top(value) {
        if (typeof value === "number") {
            this.element.style.top = `${value}px`;
        }
        else if (typeof value === "string") {
            this.element.style.top = value;
        }
    }
    get left() {
        return parseFloat(this.element.style.left);
    }
    set left(value) {
        if (typeof value === "number") {
            this.element.style.left = `${value}px`;
        }
        else if (typeof value === "string") {
            this.element.style.left = value;
        }
    }
    get width() {
        return parseFloat(this.element.style.width);
    }
    set width(value) {
        if (typeof value === "number") {
            this.element.style.width = `${value}px`;
        }
        else if (typeof value === "string") {
            this.element.style.width = value;
        }
    }
    get height() {
        return parseFloat(this.element.style.height);
    }
    set height(value) {
        if (typeof value === "number") {
            this.element.style.height = `${value}px`;
        }
        else if (typeof value === "string") {
            this.element.style.height = value;
        }
    }
    get hidden() {
        return this.element.style.display === "none";
    }
    set hidden(value) {
        if (value) {
            this.element.style.display = "none";
        }
        else {
            this.element.style.display = null;
        }
    }
    update() {
        const rect = this.ref.getBoundingClientRect();
        this.width = this.config.width ? this.config.width : rect.width;
        this.height = "auto";
        this.top = rect.top + rect.height + 5 + window.scrollY;
        this.left = rect.left + window.scrollX;
    }
    show() {
        this.hidden = false;
        if (this.onShow)
            this.onShow();
    }
    hide() {
        this.hidden = true;
        if (this.onHide)
            this.onHide();
    }
    toggle() {
        if (this.hidden) {
            this.show();
        }
        else {
            this.hide();
        }
    }
}
// Static properties
Popper.isInitialized = false;
Popper.instances = [];
