import FormControl from "../FormControl";
import { FormControlConfigDefaults } from "../FormControlConfig";
export default class TextInput extends FormControl {
    constructor(element, config = FormControlConfigDefaults) {
        super(element, Object.assign(Object.assign({}, config), { type: config.type ? config.type : TextInput.className }));
        if (!this.value)
            this.value = "";
        // Value change listener
        this.element.addEventListener("input", (event) => {
            this.value = event.target.value;
        });
    }
    get value() {
        const value = this._value;
        if (typeof value === "string")
            return value;
        else if (value)
            return value + "";
        else
            return null;
    }
    set value(value) {
        this._value = value;
        this.element.setAttribute("value", value);
    }
}
TextInput.className = "input-text";
TextInput.defaultSelector = `input[type="text"]`;
