import Button from "../../../Button/Button";
import { FormControlConfigDefaults } from "../FormControlConfig";
import TextInput from "../TextInput/TextInput";
export default class PasswordInput extends TextInput {
    constructor(element, config = FormControlConfigDefaults) {
        super(element, Object.assign(Object.assign({}, config), { type: config.type ? config.type : PasswordInput.className }));
        this.initToggleButton();
    }
    initToggleButton() {
        this.toggleButton = Button.create({
            icon: "visibility",
            variant: "transparent"
        });
        this.element.after(this.toggleButton.element);
        this.toggleButton.addClickListener(() => {
            this.toggleVisibility();
        });
    }
    toggleVisibility() {
        if (this.element.getAttribute("type") === "password") {
            this.element.focus();
            this.element.setAttribute("type", "text");
            this.toggleButton.icon = "visibility_off";
        }
        else if (this.element.getAttribute("type") === "text") {
            this.element.focus();
            this.element.setAttribute("type", "password");
            this.toggleButton.icon = "visibility";
        }
    }
}
PasswordInput.className = "input-password";
PasswordInput.defaultSelector = `input[type="password"]`;
