import Button from "../../../Button/Button";
import { FormControlConfigDefaults } from "../FormControlConfig";
import TextInput from "../TextInput/TextInput";
export default class SearchInput extends TextInput {
    constructor(element, config = FormControlConfigDefaults) {
        super(element, Object.assign(Object.assign({}, config), { type: config.type ? config.type : SearchInput.className }));
        this.element.setAttribute("type", "text");
        this.initSearchButton();
    }
    initSearchButton() {
        this.searchButton = Button.create({
            icon: "search",
            variant: "transparent"
        });
        this.element.after(this.searchButton.element);
    }
}
SearchInput.className = "input-search";
SearchInput.defaultSelector = `input[type="search"]`;
