import Component from "../../../../core/Component/Component";
import TemplateLoader from "../../../../core/View/TemplateLoader";
import Template from "./SelectOption.html";
export default class SelectOption extends Component {
    constructor(parent, option = {}) {
        const element = TemplateLoader.renderDOM(Template, Object.assign(Object.assign({}, option), { selectedIndex: parent.selectedOptionIndex })).firstElementChild;
        super(element);
        this.parent = parent;
        this.option = option;
        this.refreshElement();
        this.initEvents();
    }
    refreshElement() {
        this.element.classList.remove("selected");
        if (this.parent.selectedOptionIndex === this.option.index) {
            this.element.classList.add("selected");
        }
    }
    initEvents() {
        this.clickEvents = [];
        this.element.addEventListener("mousedown", () => {
            this.clickEvents.forEach(handler => {
                handler();
            });
        });
    }
    addClickListener(handler) {
        this.clickEvents.push(handler);
    }
    ;
}
